body {
  margin: 0;
  font-size: 16px;
}

a {
  color: #fdfdfd;
  font-weight: bold;
  text-decoration: none;
  -webkit-transition: color 0.4s;
  -o-transition: color 0.4s;
  transition: color 0.4s;
}

a:hover {
  color: #1976d2;
}

.text-center {
  text-align: center;
}

.top-bar {
  padding: 10px 15px;
  margin-bottom: 50px;
}
.top-bar::after {
  content: '';
  display: block;
  clear: both;
}

.top-bar-left {
  float: left;
  font-size: 1.5em;
}

.top-bar-right {
  float: right;
}

.top-bar a,
.nav a {
  margin: 0 8px;
}

.container {
  margin: 0 auto;
  text-align: center;
  width: 500px;
}

.card-heading {
  padding: 16px;
}

.field-line, .button-line {
  padding: 16px;
}

.error-message {
  padding: 0 16px;
  color: tomato;
}

.success-message {
  padding: 0 16px;
  color: green;
}

.listItem-container {
  margin: 0 auto;
  width: 500px;
}

.listItem-link {
  color: purple;
}

.addGame-container {
  margin: 0 auto;
  margin-bottom: 5px;
  width: 500px;
}

.userListItem-container {
  margin: 0 auto;
  width: 500px;
}
.deleteButton {
  width: 30px;
}


